.jsad-menu{
    position: relative;
.js-admenu-layout{
    overflow-y: scroll ;
    position: fixed;
    left: 0;
    top: 0;
    width: 270px;
    height: 100vh;
    background-color: var(--white);
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.05);
    z-index: 1;
    .footer{
        position: absolute;
        bottom: 0;
        padding: 0px 40px;
        text-align: center;
        z-index: -1;
        margin-top: auto;
    }
    ul{
        background-color: var(--white);
        margin: 0;
        li{
            list-style: none;
        }
    }
    .js-admenu-top-menu{
        .js-admenu-menu-control{
            list-style: none;
            height: 90px;
            position: relative;
            a{
                position: absolute;
                font-size: 24px;
                padding: 12px 16px;
                margin: 25px;
                color: var(--gray);
                
            }
        }
    }
}
    .ad-admenu-bottom-menu{
        margin-top: 25px!important;
        padding: 25px;
        .collapsed {
            .jsad-menu-collapsed{
                text-decoration: none;
                font-size: 14px;
                &:hover{
                    color: var(--primary);
                }
                span{
                    padding: 12px 13px;
                    font-size: 24px;
                    display: inline-block;
                }
            }
                &.jsad-list{
                    position: relative;
                    color: var(--mediumgray);
                    &::after{
                        content: "";
                        display: none;
                    }
                    .jsad-menu-collapsed, span{
                        color: var(--mediumgray);
                    }
                }
        }
        
        .jsad-menu-collapsed{
            cursor: pointer;
            display: flex;
            text-decoration: none;
            font-size: 14px;
            &:hover{
                color: var(--primary);
            }
            span{
                padding: 12px 13px;
                font-size: 24px;
                display: inline-block;
            }
            h6{
                line-height: 39px;
                font-weight: 400;
                color: var(--mediumgray);
                font-size: 14px;
            }
        }
        .jsad-list{
            margin:9px 0px;
            position: relative;
            color: var(--primary);
            &::after{
                content: "";
                height: 100%;
                position: absolute;
                height: 100%;
                width: 5px;
                left: -25px;
                top: -8px;
                height: 64px;
                background: var(--primary);
            }
            .jsad-menu-collapsed, span{
                color: var(--primary);
            }
        }
        .sidebar-submenu{
            padding: 5px 25px;
            ul{
                border-left: solid 1px var(--mediumgray);
                li{
                    padding: 16px 0px 16px 16px;
                    text-align: left;
                    a{
                        text-decoration: none;
                        font-weight: normal;
                        font-size: 14px;
                        text-align: center;
                        color: var(--mediumgray);
                        span{
                            display: none;
                        }
                        h6{                            
                        text-decoration: none;
                        font-weight: 400;
                        font-size: 14px;
                        text-align: left;
                        color: var(--mediumgray);
                        }

                    }
                }
            }
        }
    }
}
.jsad-layout{
    .jsad-topmenu{
        position: absolute;
        width: 100%;
        background-color: #fff;
        box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.02);
        z-index: 0;
        .jsad-flex{
            
        .jsad-user-layout{
            display: flex;
            flex-wrap: wrap;
            height: 95px;
            width: 190px;
            flex: 1;
            .jsad-control-left{
                height: 95px;
                box-shadow: inset  -1px 0px 2px rgba(0, 0, 0, 0.05);
                display: inline-grid;
                padding: 0px 15px;
                img{
                    margin: auto;
                    border-radius: 50px;
                    overflow: hidden;
                    
                }
            }
            .jsad-control-right{
                width: 92px;
                height: 95px;
                display: inline-grid;
                span{
                    margin: auto;
                    justify-content: center;
                    font-size: 20px;
                }
                
            }
        }
        }
    }
}
.jsad-body-layout{
    overflow: hidden;
    margin-left: 270px;
    margin-right: 191px;
    // padding: 192px 19px 50px;
    padding: 130px 19px 50px;
    min-height: 100vh;
    .jsad-body-layout-box{
        text-align: left;
        margin-top: 20px;
        overflow: hidden;
        height: calc(100% - 40px);
        padding: 0;
        border-radius: 4px;
        background: #fff;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
        .padding{
            padding: 35px 40px;
        }
    }
}