// $mq-900-down: "only screen and (max-width: 899px)";
// $mq-900-up: "only screen and (min-width: 900px)";

// $mq-1000-down: "only screen and (max-width: 999px)";
// $mq-1000-up: "only screen and (min-width: 1000px)";

// $mq-1180-up: "only screen and (min-width: 1180px)";

// $mq-1400-up: "only screen and (min-width: 1400px)";
// The mixins can be defined in some shared file
@mixin media-sm {
    @media screen and (min-width: 768px) {
      @content;
    }
  }
  @mixin media-lg {
    @media screen and (min-width: 1200px) {
      @content;
    }
  }
  
  .search-copyright-tab .copyright {
    @include media-sm {
      font-size: 19px;
    }
    @include media-lg {
      font-size: 21px;
    }
  }
  body{
      &.collap{
          .jsad-menu{
              .js-admenu-layout{
                width: 100px;
                .ad-admenu-bottom-menu{
                    .jsad-list{
                        .jsad-menu-collapsed{
                            h6{
                                display: none;
                            }
                        }
                        .sidebar-submenu{
                            padding: 0;
                            ul{
                                border: none;
                            }
                        }
                    }
                }
              }
              .ad-admenu-bottom-menu .sidebar-submenu ul {
                li {
                    padding: 10px;
                    a{
                            &:hover h6{
                                display: block;
                                position: absolute;
                                background-color: #fff;
                            }
                        span{
                            display: block;
                        }
                        h6{
                            display: none;
                        }
                      }
                }
              }
              
          }
      }
  }