ul{
    li{
        list-style: none;
    }
}
.jsad-dashboard-menu{
    display: inline-flex;
    list-style: none;
    width: 100%;
    li{
        padding: 35px 0px;
        width: 50%;
        float: left;
            text-align: center;
        span{
            font-size: 20px;
            margin: auto;
            display: block;
            &.icon{
                overflow: hidden;
                border-radius: 50%;
                height: 80px;
                width: 80px;
                padding: 28px 0px;
            }
        }
        h5{
            font-weight: 500;
            font-size: 18px;
            line-height: 20px;
            text-align: center;
            color: #2d2f39;
            }
            p{
                font-weight: 500;
            }
    }
}.jsad-dashboard-menu{
    &.single{
        position: relative;
        // width: 200px;
        &.border-right-after{
        &::after{
            position: absolute;
            right: 0;
            top: -20px;
            margin: 40px 0;
            content: "";
            height: 130px;
            width: 2px;
            background-color: var(--graymedium);
        }

        }
            li{
                // width: 200px;\
                width: 100%;
                margin:auto;
                    span{
                        font-size: 24px;
                    }
            }
        a{
            text-decoration: none;
            &:hover{
                color: var(--primary);
            }
        }
    }
}
.orange{
    color: var(--primary);
    background-color: var(--secondary);
    margin: auto;
}
.yellow{
    color: var(--yellow);
    background-color: var(--lightyellow);
    margin: auto;
}
.blue{
    color: var(--blue);
    background-color: var(--lightblue);
    margin: auto;
}
.green{
    color: var(--green);
    background-color: var(--lightgreen);
    margin: auto;
}
.vilote{
    color: var(--vilote);
    background-color: var(--lightvilote);
    margin: auto;
}
.l-yellow{
    color: var(--yellow);
    background-color: var(--lightyellow);
    h3{
        color: var(--yellow);
    }
}
.l-blue{
    color: var(--blue);
    background-color: var(--lightblue);
    h3{
        color: var(--blue);
    }
}
.l-vilote{
    color: var(--vilote);
    background-color: var(--lightvilote);
    h3{
        color: var(--vilote);
    }
}
.l-green{
    color: var(--green);
    background-color: var(--lightgreen);
    h3{
        color: var(--green);
    }
}