// scroll

/* width */
::-webkit-scrollbar {
	width: 1px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
	background: var(--secondary);
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
	background: var(--primary);
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
	background: var(--red);
  }
