form{
    &.was-validated{
    .form-control{
        &:invalid{
            &~.invalid-feedback{
                display: block;
            }
          }
    }
    }}
  .form-group {
      padding-top: 10px;
      padding-bottom: 10px;
    position: relative;
    .fa{
        position: absolute;
        right: 0;
        top: 12px;
        font-size: 15px;
    }
      &>label {
          bottom: 34px;
          position: relative;
          background-color: white;
          padding: 0px 5px 0px 5px;
          font-size: 1.1em;
          transition: 0.2s;
          pointer-events: none;
          font-family: 'Roboto', sans-serif !important;
          color: var(--primary);
          font-weight: normal;
          font-size: 14px;
          font-weight: 500;
          text-align: left;
          color: var(--gray);
          background-color: transparent;
          font-weight: 600;
        //   display: none;
      }

      .form-control {
          border-radius: 0;
          border-color: transparent;
          border-bottom: solid 1px gray;
        
          box-shadow: none;
          &:focus {
              color: #495057;
              background-color: #fff;
              border-color: transparent;
              border-bottom: solid 1px red;
              outline: 0;
              box-shadow: none;

              &~label {

                  bottom: 55px;

              }
          }

          &:valid {
              &~label {
                  bottom: 55px;
              }
          }
      }
      .small-text{
        font-weight: 300;
        font-size: 12px;
        text-align: left;
        color: #8d999e;
        box-shadow: none;
        margin-top: -10px;
      }
  }
  
  .form-row{
      position: relative;
      label{
            top: 10px;
            position: absolute;
            transition: 0.2s;
      }
      .invalid-feedback{
          margin-top: 10px;
      }
    textarea{
        &.form-control{
            border-radius: 0;
            border-color: transparent;
            border-bottom: 1px solid grey;
            box-shadow: none;
            // display: none;
        &:focus {
          color: #495057;
          background-color: #fff;
          border-color: transparent;
          border-bottom: solid 1px red;
          outline: 0;
          box-shadow: none;
          &~label {
              position: absolute;
              top: -10px;

          }
      }
      &:valid {
          &~label {
              position: absolute;
              top: -10px;
          }
      }
        }
    }
}

  .form-control.is-invalid,
  .was-validated .custom-select:invalid,
  .was-validated .form-control:invalid {
      border-color: transparent !important;
      border-bottom: solid 1px red !important;
  }

  .form-control.is-invalid:focus,
  .was-validated .custom-select:invalid:focus,
  .was-validated .form-control:invalid:focus {
      box-shadow: 0 0 0 0.2rem #fff !important;
  }
  .custom-select.is-valid, .form-control.is-valid, .was-validated .custom-select:valid, .was-validated .form-control:valid {
    
    border-color: transparent !important;
    border-bottom: solid 1px var(--green) !important;
}
.custom-select.is-valid, .form-control.is-valid, .was-validated .custom-select:valid, .was-validated .form-control:valid{
    
    box-shadow: 0 0 0 0.2rem #fff !important;
}
  .invalid-feedback {
      display: none;
      width: 100%;
      margin-top: 0rem;
      font-size: 80%;
      color: #dc3545;
  }
  .form-group.-hasvalue ~ .label, .form-group:focus ~ .label {
    transform: translateY(-100%) translateY(-0.5em) translateY(-2px) scale(0.9);
    cursor: pointer;
    pointer-events: auto;
}
.form-row.-hasvalue ~ .label, .form-row:focus ~ .label {
    transform: translateY(-100%) translateY(-0.5em) translateY(-2px) scale(0.9);
    cursor: pointer;
    pointer-events: auto;
}
.checkbox-box{
    margin-bottom: 0;
    label{
      line-height: 20px;
        bottom: 0;
        margin-left: 0px;
        font-weight: normal;
        font-size: 12px;
        text-align: left;
        color: #8d999e;
        vertical-align: sub;
        padding-left: 0px;
    }
    input[type=checkbox]{
        margin-left: 0;
        display: none;
        &:checked {
            &+ label{
                &:before{
                    border-color: var(--primary);
                    color: #fff;
                    content: "\f00c";
                    color: var(--primary);
                    }
            }
        }
        &+label:before{
            content: "\f00c";
            border: 0.1em solid var(--primary);
            // border-radius: 0.2em;
            display: inline-block;
            width: 1em;
            height: 1em;
            padding-left: .2em;
            padding-bottom: .3em;
            padding-right: 0.9rem;
            padding-bottom: 1rem;
            margin-right: 0.2em;
            vertical-align: bottom;
            transition: .2s;
            font-family: FontAwesome;
            text-decoration: inherit;
            color: transparent;
            font-size: 15px;
        }
    }
}
.role-form{
    label{
        color: var(--gray);
    }
}



.radio {
    label{
        padding-left: 0;
    }
  input[type="radio"] {
    position: absolute;
    opacity: 0;
    display: none;
    + .radio-label {
      &:before {
        content: '';
        background: var(--white);
        border-radius: 100%;
        border: 1px solid darken(#ff5242, 25%);
        border: solid 1px var(--primary);
        display: inline-block;
        width: 1.4em;
        height: 1.4em;
        position: relative;
        top: -0.2em;
        margin-right: 1em; 
        vertical-align: top;
        cursor: pointer;
        text-align: center;
        transition: all 250ms ease;
      }
    }
    &:checked {
      + .radio-label {
        &:before {
          background-color: var(--white);
          box-shadow: inset 0 0 0 4px var(--primary);
        }
      }
    }
    &:focus {
      + .radio-label {
        &:before {
          outline: none;
          border-color: var(--white);
        }
      }
    }
    &:disabled {
      + .radio-label {
        &:before {
          box-shadow: inset 0 0 0 4px #ff5242;
          border-color: darken(#ff5242, 25%);
          background: darken(#fff, 25%);
        }
      }
    }
    + .radio-label {
      &:empty {
        &:before {
          margin-right: 0;
        }
      }
    }
  }
}
