.profile-pic {
    border-radius: 3px;
    overflow: hidden;
    background: transparent;

    .input-profile-pic-uplade {
        position: relative;
        height: 50px;
        width: 100%;
        background-color: var(--primary);

        .profile-pic-input {
            height: 50px;
            width: 100%;
            opacity: 0;
            position: relative;
            z-index: 10;
        }

        .fa {
            position: absolute;
            left: 20px;
            top: 10px;
            color: var(--white);
            font-size: 25px;
            z-index: 0;
        }

        label {
            position: absolute;
            top: 10px;
            left: 50px;
            color: var(--white);
            font-size: 18px;
            font-weight: 400;
            z-index: 0;
        }
    }
}

.uploadcare--widget{
    display: flex;
}
.uploadcare--widget__button{
    width: 100%;
    height: 50px;
    opacity: 0;
}
.uploadcare--widget__text{
    opacity: 0;
}
.uploadcare--link,.uploadcare--powered-by {
    display: none;
}