.login-layout {
    .glass-layout {
        display: flex;
        justify-content: center;
        flex-direction: column;
        margin: 0px 20px;
        box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);
        border-radius: 5px;
        position: relative;
        z-index: 1;
        background: inherit;
        overflow: hidden;
        padding: 0px 40px;
        h2{
            margin-top: 50px;
            font-weight: 800;
        }
        h2,a,p{
            text-align: left;
            text-decoration: none;
        color: #fff;
        }

        &:before {
            content: "";
            position: absolute;
            background: inherit;
            z-index: -1;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            box-shadow: inset 0 2000px #ffffff6b;
            -webkit-filter: blur(10px);
            filter: blur(10px);
            margin: -20px;
        }
        }

        img {
            justify-content: center;
            width: 180.36px;
            height: 179.25px;
            background: transparent;
            margin: auto;
            margin-top: 50px;

        }
        form{
            .form-group{
                    &>label {
                        bottom: 34px;
                        position: relative;
                        padding: 0px 5px 0px 5px;
                        transition: 0.2s;
                        pointer-events: none;
                        font-family: 'Roboto', sans-serif !important;
                        color: var(--white)!important;
                        font-size: 14px;
                        text-align: left;
                        background-color: transparent;
                        font-weight: 600;
                    }
                    .fa{
                        color: #fff;
                    }
                .form-control{
                    color: #fff;
                    background-color: transparent;
                    border-bottom: solid .1px white;
                    &:focus {
                        background-color: rgba(255, 255, 255, 0);
                        border-color: transparent;
                        border-bottom: solid 1px red;
                        outline: 0;
                        box-shadow: none;
          
                        &~label {
          
                            bottom: 55px;
          
                        }
                    }
                    &:valid {
                        &~label {
                            bottom: 55px;
                        }
                    }
                }   
                .btns{
                    width: 100%;
                    padding: 13px 15px;
                    font-size: 15px;
                    height: auto;
                }
                .ls-login{
                    display: flex;
                    width: 100%;
                    li{
                        float: left;
                        flex: 1;
                        margin:5px;
                    }
                }
            }
        }
    .footer{
        text-align: center;
        margin: 40px 0px;
    }
    
    
}

.top-156 {
    margin-top: 15%;
}
.text-red{
    color: red!important;
}