.modal-dialog{
    &.center{
        margin: 10% auto;
    }
.modal-content{
    border-radius: 2px;
    .modal-header{
        border-bottom:solid 0px;    
        & .close{
            background-color: var(--secondary);
            padding: 20px;
            border-radius: 50%;
            color: var(--primary);
            opacity: 10;
            position: absolute;
            right: -20px;
            top: -20px;
            height: 55px;
            span{
                font-size: 18px;
            }
            &:focus{
                outline: none;
            }
        }
    }
    .modal-body{
        overflow: hidden;
        padding: 30px!important;
        form{
            label{
                font-weight: 500;
                font-size: 15px;
                text-align: left;
                color: #293134;
                font-weight: 600;
            }
        }
    }
}
}