.btns {
    width: 98px;
    height: 36px;
    border-radius: 4px;
    border: solid 0px;
    transition: background .5s;
}

.btn-green {
    color: var(--white);
    background: #61d162;

    &:hover {
        background: var(--primary);
    }
      
    &:focus {
        outline: none;
    }
}

.btn-red {
    color: var(--white);
    background: var(--primary);

    &:hover {
        background: var(--green);
    }

    &:focus {
        outline: none;
    }
}
.btn-blue {
    color: var(--white);
    background: blue;

    &:hover {
        background: var(--primary);
    }

    &:focus {
        outline: none;
    }
}
    .secondary-btn {
        border-radius: 21.5px;
        background: #fff;
        box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
        padding: 12px 15px;
        font-weight: normal;
        font-size: 14px;
        line-height: 60px;
        text-align: left;
        color: #ff5242;
        text-decoration: none;
        transition: background-color .5s;
        transition: color .5s;

        &:hover {
            outline: none !important;
            border-color: none !important;
        }

        &:focus {
            outline: none !important;
            border-color: none !important;
        }
    }

    .btn-primary {
        &:hover {
            color: var(--white);
            text-decoration: none;
            background-color: var(--primary);
            outline: none;
            border-color: none;
        }

        &:focus {
            color: var(--white);
            text-decoration: none;
            background-color: var(--primary);
            outline: none;
            border-color: none;
        }
    }
    

    a {
        text-decoration: none;
    }