.dataTables_wrapper{
    .row{
        margin: 0;
        &:nth-child(2){
            margin-top: 20px;
            margin-bottom: 20px;
        }
        .dataTable{
            width: 100%;
            overflow: hidden;
            thead{
                tr{
                    th{   
                        border: solid .5px var(--lightgray);           
                        padding: 15px 10px; 
                        font-weight: normal;
                        font-size: 14px;
                        text-align: left;
                        color: #707070;
                background: #fff;
                border: 1px solid #f1f2f2;
                box-shadow: 10px 2px 7px rgba(0, 0, 0, 0.275);

                    &::after{
                        top: 15px;
                    }
                    }
                }
            }
            tbody{
                tr{
                    td{   
                        border: solid .5px var(--lightgray);           
                        padding: 15px 10px; 
                        font-weight: normal;
                        font-size: 14px;
                        text-align: left;
                        color: #707070;
                        &:nth-last-child(1){
                            ul{
                                display: flex;
                                flex-wrap: wrap;
                                justify-content: space-around;
                                li{
                                    float: left; 
                                    font-size: 15px;      
                                    a{                                            
                                        &:hover{
                                            color: var(--primary);
                                        }
                                    }
                                    &+li{
                                        margin-left: 10px;  
                                    }
                                }
                            }
                        }
                    }
                    &:nth-child(odd){
                        background-color: var(--secondary);
                        td{   
                            border: solid .5px var(--primary);           
                            padding: 15px 10px; 
                            font-weight: normal;
                            font-size: 14px;
                            text-align: left;
                            color: #000;
                        }
                    }
                }
            }
        }
    }
    .dataTables_paginate{
        .pagination{
            .paginate_button{
                a{
                    background-color: var(--secondary);
                    border-radius: 0;
                    border: solid 1px var(--primary);
                    color:  var(--primary);
                }
                &.active{
                    a{
                        background-color:  var(--primary);
                        color:  var(--white);
                        outline: 0;
                    }
                }
            }
        }
    }
    .dataTables_info{
        display: none;
    }
        .form-control{
            height: 40px;
            border-radius: 5px;
            background: #fff;
            border: 1px solid #ff5242;
            box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
    }
}

.switch {
    display: inline-block;
    height: 18px;
    position: relative;
    width: 30px;
    input {
  display:none;
  &:checked {
      &+ .slider {
        background-color: var(--green);
      }    
      &+ .slider:before {
        transform: translateX(12px);
      }               
  }
}
}
.slider {
  background-color:#424242;
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: .4s;
  &:before {
    background-color: #fff;
    bottom: 0px;
    content: "";
    height: 18px;
    left: 0px;
    position: absolute;
    transition: .4s;
    width: 18px;                                          
  }   
  &.round {
    border-radius: 34px;
  }      
  &:before {
    border-radius: 50%;
  } 
}    





